import React from 'react';
import "@/sass/header.scss";
import menu from 'config/menu';

const additionalPosition = -120;
const scrollBehaviour = "smooth";

class List extends React.Component<{}> {

    scrollTo(url: string) {
        const part = url.split("#");
        const menuID = part[1];
        const el = document.getElementById(menuID);
        const position = el ? (el.offsetTop + additionalPosition) : 0
        window.scrollTo({
            top: position,
            behavior: scrollBehaviour
        });
    }

    render() {
        return (
            <ul className="navbar-nav menu-list">
                { menu.map((m) => {
                    const handleClick = (e: any) => {
                        e.preventDefault()
                        this.scrollTo(e.target.href)
                    }
                    return (
                        <li className="nav-item" key={m.key}>
                            <a className="menu-item nav-link" href={m.key} onClick={handleClick}>{m.label}</a>
                        </li>
                    )
                })}
            </ul>
        )
    }
}

export default List